.header {
    /* height: 60px; */
    background-color: #131921;
    display: flex;
    align-items: center;
    position: sticky;
    top: 0;
    z-index: 100;
    margin-top: 0px;
}

.header__logo {
    width: 100px;    
    margin-left: 20px;
    margin-right: 20px;
    margin-top: 18px;
    object-fit: contain;
}

.header__search {
    display: flex;
    flex: 1;
}

.header__searchIcon {
    padding: 5px;
    height: 22px important;
    background-color: rgb(243, 171, 16);
}

.header__searchInput {
    height: 12px;
    padding: 10px;
    border: none;
    width: 100%;
}

.header__nav {
    display: flex;
    justify-content: space-evenly;
}

.header__link {
    text-decoration: none;
    color: whitesmoke;
}

.header__option {
    display: flex;
    flex-direction: column;
    margin-left: 10px;
    margin-right: 10px;
}

.header__option__line1 {
    font-size: 13px;
    font-weight: 800;
}

.header__optionBasket {
    display: flex;
    align-items: center;
}

.header__basketCount {
    margin-left: 10px;
    margin-right: 10px;
}